import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { paramsApplier as applyParams } from 'react-router-sitemap';

import PRODUCTS_QUERY from '../../graphql/SiteMapProds.graphql';

// const paths = ['/products/:category', '/products/brands/:vendor_name', '/product/:vendor_name/:product_name/:category']

export default class SiteMapGenerator extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  componentDidMount() {
    console.log('THIS SITE MAP PROPS::', this.props);
  }
  componentDidUpdate(prevProps, prevState) {
    console.log('UPDATE PROPS::', this.props);
    console.log('UPDATE PREV PROPS:', prevProps);
  }
  generateSiteMap = async () => {
    const products = await this.getAllProducts();
    // const generated = await this.props.createSiteMap();
    // console.log('GENERATED::', generated);
  };
  getAllProducts = async () => {
    const { currEndCursor } = this.props.products;
    // console.log('THE END CURSOR::', currEndCursor);
    const {
      data: {
        admin_products: { pageInfo }
      }
    } = await this.props.loadMore(250, currEndCursor);
    if (pageInfo.hasNextPage) {
      return this.getAllProducts();
    } else {
      // console.log('THE PROPS;:::', this.props.admin_products);
      const { edges } = this.props.admin_products;
      // console.log('EDGES::', edges);
      this.generateProductsConfig(edges);
    }
  };
  generateProductsConfig = async products => {
    // const path = ['/product/:vendor_name/:product_name/:category'];
    console.log('PRODUCTS::', products);
    const pathFormater = string =>
      string
        .split(' ')
        .join('-')
        .toLowerCase();
    const paramConfig = products.reduce((accum, { node }) => {
      // console.log('CURR::', node);
      accum = [
        ...accum,
        { vendor_name: pathFormater(node.vendor), product_name: node.handle, category: pathFormater(node.productType) }
      ];
      // console.log('ACCUM LENGTH', accum, accum.length);
      return accum;
    }, []);
    const config = {
      '/product/:vendor_name/:product_name/:category': paramConfig
    };
    console.log('CONFIG::', config);
  };
  render() {
    const { productsLoading, admin_products } = this.props;
    console.log('ADMIN PRODUCTS::', admin_products);

    return (
      <section>
        <div>SiteMap</div>
        <button onClick={this.generateSiteMap}>Create Site Map</button>
      </section>
    );
  }
}
SiteMapGenerator.propTypes = {
  createSiteMap: PropTypes.func,
  admin_products: PropTypes.object,
  productsLoading: PropTypes.bool
};
